<template>    <n-space justify="space-between">        <n-space style="padding: 0">            <n-button text style="font-size: 24px" @click="back">                <n-icon :component="ArrowBackOutline"/>            </n-button>            <n-button text style="font-size: 24px" @click="forward">                <n-icon :component="ArrowForwardOutline"/>            </n-button>        </n-space>        <n-space style="padding: 0" align="center">            <n-dropdown v-if="user" trigger="click" :options="options" @select="handleSelect">                <div class="user-detail">                    <n-avatar :src="user === null ? null : user.avatar" round :size="30" :fallback-src="notImage"/>                    <span style="padding-left: 10px">{{ user === null ? '' : user.username }}</span>                </div>            </n-dropdown>            <n-switch v-model:value="playStore.isDark" style="padding: 0; margin: 0" size="large">                <template #unchecked-icon>                    <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 512 512">                        <path d="M234 26h44v92h-44z" fill="currentColor"></path>                        <path d="M234 394h44v92h-44z" fill="currentColor"></path>                        <path d="M338.025 142.857l65.054-65.054l31.113 31.113l-65.054 65.054z"                              fill="currentColor"></path>                        <path d="M77.815 403.074l65.054-65.054l31.113 31.113l-65.054 65.054z"                              fill="currentColor"></path>                        <path d="M394 234h92v44h-92z" fill="currentColor"></path>                        <path d="M26 234h92v44H26z" fill="currentColor"></path>                        <path d="M338.029 369.14l31.112-31.113l65.054 65.054l-31.112 31.112z"                              fill="currentColor"></path>                        <path d="M77.802 108.92l31.113-31.113l65.054 65.054l-31.113 31.112z" fill="currentColor"></path>                        <path d="M256 358a102 102 0 1 1 102-102a102.12 102.12 0 0 1-102 102z"                              fill="currentColor"></path>                    </svg>                </template>                <template #checked-icon>                    <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 512 512">                        <path                            d="M264 480A232 232 0 0 1 32 248c0-94 54-178.28 137.61-214.67a16 16 0 0 1 21.06 21.06C181.07 76.43 176 104.66 176 136c0 110.28 89.72 200 200 200c31.34 0 59.57-5.07 81.61-14.67a16 16 0 0 1 21.06 21.06C442.28 426 358 480 264 480z"                            fill="currentColor"></path>                    </svg>                </template>            </n-switch>        </n-space>    </n-space></template><script>import {NSpace, NIcon, NButton, NDivider, NSwitch, NDropdown, NAvatar} from 'naive-ui'import {ArrowBackOutline, ArrowForwardOutline} from '@vicons/ionicons5'import router from '../router'import {ref} from 'vue'import {usePlayStore} from '../store'import notImage from '../assets/notImage.jpg'export default {    name: 'AdminHeader',    components: {        NSpace,        NIcon,        NButton,        NDivider,        NSwitch,        NDropdown,        NAvatar,    },    setup() {        let playStore = usePlayStore()        let forwardPath = ref(history.state.forward)        let isDark = ref(false);        let user = JSON.parse(localStorage.getItem('user'))        let options = [            {                label: '个人信息设置',                key: '/admin/detail',            },            {                type: 'divider',                key: 'd1'            },            {                label: '退出登录',                key: "/logout"            }        ]        function back() {            router.back()        }        function forward() {            router.forward()        }        function handleSelect(key) {            if (key === '/admin/detail') {                router.push('/admin/detail')            } else if (key === '/logout') {                localStorage.removeItem('token')                router.push('/login')            }        }        return {            ArrowForwardOutline,            ArrowBackOutline,            back,            forward,            handleSelect,            isDark,            playStore,            forwardPath,            options,            user,            notImage,        }    }}</script><style scoped>.n-space {    padding: 10px;}.n-button, .n-switch {    margin-top: 4px;}.n-divider {    margin: 0;}.user-detail {    display: flex;    align-items: center;}.user-detail span {    cursor: pointer;    color: #5b5e60;}.user-detail span:hover {    cursor: pointer;}</style>