<template>    <n-space vertical>        <n-layout>            <AdminHeader/>            <n-layout has-sider>                <n-layout-sider                    bordered                    collapse-mode="width"                    :collapsed-width="64"                    :width="240"                    :collapsed="collapsed"                    show-trigger                    @collapse="collapsed = true"                    @expand="collapsed = false"                >                    <n-menu                        v-model:value="activeKey"                        :collapsed="collapsed"                        :collapsed-width="64"                        :collapsed-icon-size="22"                        :options="menuOptions"                    />                </n-layout-sider>                <n-layout>                    <n-scrollbar style="height: calc(100vh - 56px); padding: 0 20px">                        <router-view/>                    </n-scrollbar>                </n-layout>            </n-layout>        </n-layout>    </n-space></template><script>import {NIcon, NLayout, NMenu, NSpace, NLayoutSider, NScrollbar} from "naive-ui";import {h, ref, watch, reactive} from 'vue'import {RouterLink, useRoute} from "vue-router";import {    SparklesOutline,} from "@vicons/ionicons5";import AdminHeader from './AdminHeader'function renderIcon(icon) {    return () => h(NIcon, null, {default: () => h(icon)});}const menuOptions = reactive([    {        label: () => <RouterLink to='/admin/song'>管理音乐</RouterLink>,        key: "/admin/song",        icon: renderIcon(SparklesOutline)    },    {        label: () => <RouterLink to='/admin/songList'>管理歌单</RouterLink>,        key: "/admin/songList",        icon: renderIcon(SparklesOutline)    },    {        label: () => <RouterLink to='/admin/singer'>管理歌手</RouterLink>,        key: "/admin/singer",        icon: renderIcon(SparklesOutline)    },    {        label: () => <RouterLink to='/admin/user'>管理用户</RouterLink>,        key: "/admin/user",        icon: renderIcon(SparklesOutline)    }]);export default {    name: 'AdminMenu',    setup() {        let route = useRoute();        let activeKey = ref(route.path)        watch(() => route.path, (newVal) => {            activeKey.value = newVal;        });        return {            collapsed: ref(false),            menuOptions,            activeKey,        };    },    components: {        AdminHeader,        NIcon,        NLayout,        NMenu,        NSpace,        NLayoutSider,        NScrollbar,    }}</script><style scoped></style>